<template>
  <div class="template-edit">
    <h2>  </h2>
    <div v-if="loading">...</div>
    <form v-else @submit.prevent="saveTemplate">
      <div>
        <label> </label>
        <input v-model="template.code" required />
      </div>
      <div>
        <label>  :</label>
        <input v-model="template.name" required />
      </div>
      <div class="form-checkbox">
        <label for="is_active"> </label>
        <input type="checkbox" id="is_active" v-model="template.is_active" />
      </div>
      <div>
        <label>:</label>
        <select v-model="template.category">
          <option value="window"></option>
          <option value="door"></option>
          <option value="glyhar"></option>
          <option value="vitrage"></option>
          <option value="loggia"></option>
        </select>
      </div>
      <div>
        <label>(-,-,    ):</label>
        <input v-model="template.systema" />
      </div>
      <div>
        <label> ( .,  .-., 1, 1.5,2,1,1.5,2):</label>
        <input v-model="template.type_izd" />
      </div>
      <div>
        <label>(,,,,):</label>
        <input v-model="template.profile" />
      </div>
      <div>
        <label>   ( -  ):</label>
        <input v-model="template.head_name" />
      </div>

      <!--  -->
      <h3></h3>
      <table class="operations-table">
        <thead>
        <tr>
          <th class="col-label"></th>
          <th> </th>
          <th></th>
          <th>(/)</th>
          <th>(/)</th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(op, index) in template.operations" :key="index">
          <td><input v-model="op.label" /></td>
          <td><input v-model="op.name" /></td>
          <td><input type="number" step="1" min="0" v-model.number="op.count" /></td>
          <td><input type="number" step="0.001" min="0" v-model.number="op.value" /></td>
          <td><input type="number" step="0.001" min="0" v-model.number="op.minutes" /></td>
          <td><input v-model="op.group" /></td>
          <td>
            <button type="button" @click="removeOperation(index)"></button>
          </td>
        </tr>
        </tbody>
      </table>
      <button type="button" @click="addOperation" class="btn">+  </button>

      <div class="form-actions">
        <button type="submit"></button>
        <router-link to="/admin/templates" class="btn btn-outline"></router-link>
      </div>
    </form>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'

//const route = useRoute()
const router = useRouter()
const template = ref({
  code: '',
  name: '',
  category: '',
  systema: '',
  type_izd: '',
  profile: '',
  is_active: 1,
  operations: [], //    
  rules: null, //   
  head_name: '',
})
const loading = ref(false)


function addOperation() {
  template.value.operations.push({
    label: '',
    name: '',
    count: 0,
    value: 0.0,
    minutes: 0,
    group: '',
    type: 'number',     //    
    required: false      //   false,   
  })
}

function removeOperation(index) {
  template.value.operations.splice(index, 1)
}

async function saveTemplate() {
  try {
    const payload = {
      ...template.value,
      operations: template.value.operations //    
      // rules    ( null)
    }

    //console.log("PAYYY", payload);

    const res = await fetch(`/api/admin/template/new`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })

    if (!res.ok) throw new Error(`HTTP ${res.status}`)

    alert(' ')
    router.push('/admin/templates')
  } catch (err) {
    console.error(' :', err)
    alert('   ')
  }
}

onMounted(() => {
  addOperation() //    
})
</script>

<style scoped>
.template-edit {
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
}
input, select {
  width: 100%;
  padding: 6px;
  margin: 6px 0;
}
.operations-table {
  width: 100%;
  margin: 16px 0;
  border-collapse: collapse;
}

.operations-table th,
.operations-table td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}
.operations-table input,
.operations-table select {
  width: 100%;
  margin: 0;
}

.form-checkbox {
  display: flex;
  align-items: center;
  gap: 8px; /*      */
  margin: 8px 0;
}

.col-label{
  width: 30%;
}

.btn {
  padding: 8px 16px;
  margin: 6px;
  cursor: pointer;
}
.form-actions {
  margin-top: 20px;
}

</style>